.page {
  background-color: #000;
  max-width: 1280px;
  min-width: 320px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  padding: 0 200px 60px;
  position: relative;
  min-height: 100vh;
}
@media screen and (max-width: 767px) {
  .page {
    padding: 0 0 36px;
  }
}
